<template>
  <div>
    <Sidebar />
    <div class="grid__body">
      <Header />
      <main class="main">
        <router-view :user="user" :key="keyIndex"/>
      </main>
      <Footer />
    </div>
  </div>
</template>

<script>
import Sidebar from '../components/Sidebar';
import Footer from '../components/Footer';
import Header from '../components/Header';
import eventHub from '../utils/eventhub';

export default {
	name: 'Base',
	components: { Sidebar, Footer, Header },
	data() {
		return {
			user: {},
			keyIndex: 0
		};
	},
	created() {
		eventHub.$on('rerender', () => {
			this.forceRerender();
		});
	},
	mounted() {
		this.user = this.$session.getAll();
	},
	methods: {
		forceRerender() {
			this.keyIndex += 1;
		}
	}
};
</script>
